<template>
  <b-container fluid>
    <b-row cols="12" id="myNav">
      <TheNavBar />
    </b-row>

    <b-row align-v="center" class="main">
      <b-col cols="12">
        <TheMain />
      </b-col>
    </b-row>

    <b-row align-v="center" id="about">
      <b-col cols="1"> </b-col>
      <TheAboutInfo />
      <b-col cols="1"> </b-col>
    </b-row>

    <b-row align-v="center" id="projects" style="background-color: #505a5c">
      <b-col cols="12">
        <br />
        <h1 style="color: white">
          <font-awesome-icon icon="fa-solid fa-diagram-project" /> Projects
        </h1>
        <br />
      </b-col>
    </b-row>

    <b-row align-v="center" style="background-color: #505a5c">
      <b-col cols="12">
        <b-card-group deck>
          <ProjectCard
            v-for="project in projects"
            :key="project.id"
            :name="project.name"
            :description="project.description"
            :imgSrc="project.imgSrc"
            :github="project.github"
          ></ProjectCard>
        </b-card-group>
      </b-col>
    </b-row>

    <b-row align-v="center" style="background-color: #505a5c">
      <b-col>
        <br />
        <br />
        <br />
      </b-col>
    </b-row>

    <TheFooter />
  </b-container>
</template>

<script>
// @ is an alias to /src
import ProjectCard from "../components/ProjectCard.vue";
import TheNavBar from "../components/TheNavBar.vue";
import TheFooter from "../components/TheFooter.vue";
import TheMain from "../components/TheMain.vue";
import TheAboutInfo from "../components/TheAboutInfo.vue";

export default {
  name: "Home",
  components: {
    ProjectCard,
    TheNavBar,
    TheFooter,
    TheMain,
    TheAboutInfo,
  },
  mounted() {
    this.fetchData();
  },
  data() {
    return {
      projects: [],
    };
  },
  methods: {
    async fetchData() {
      const res = await fetch("projects.json");
      const val = await res.json();
      this.projects = val;
    },
  },
};
</script>

