<template>
  <b-card-text align="start">
    <b-row>
      <b-col cols="12" align="center">
        <h5>LV: {{ lv }} Wizard</h5>
      </b-col>
    </b-row>
    <h5 align="start">
      EXP
      <button id="expUp" @click="checkXp()">
        <font-awesome-icon icon="fa-solid fa-circle-up" />
      </button>
    </h5>
    <b-progress max="100" class="mb-3">
      <b-progress-bar variant="primary" :value="exp"></b-progress-bar>
    </b-progress>
    <h5>Abilities</h5>
    <p>
      Static: The opponent has a 30% chance of being induced with PARALYZE when
      using an attack.
    </p>
    <b-row align="start">
      <b-col cols="6">
        <h5>HP: {{ hp }}</h5>
      </b-col>
      <b-col cols="6">
        <h5>SPEED: {{ speed }}</h5>
      </b-col>
    </b-row>
    <br />
    <b-row align="start">
      <b-col cols="6">
        <h5>ATK: {{ atk }}</h5>
      </b-col>
      <b-col cols="6">
        <h5>DEF: {{ def }}</h5>
      </b-col>
    </b-row>
    <br />
    <b-row align="start">
      <b-col cols="6">
        <h5>SP. ATK: {{ spatk }}</h5>
      </b-col>
      <b-col cols="6">
        <h5>SP. DEF: {{ spdef }}</h5>
      </b-col>
    </b-row>
  </b-card-text>
</template>

<script>
export default {
  name: "Stats",
  data() {
    return {
      lv: 1,
      exp: 0,
      hp: 35,
      atk: 55,
      def: 40,
      spatk: 50,
      spdef: 50,
      speed: 90,
    };
  },
  methods: {
    lvUp() {
      if (this.exp % 100 == 0) {
        this.exp = 0;
        if (this.lv < 100) {
          this.lv += 1;
          this.improveStats();
        }
      }
      return this.exp;
    },
    increment() {
      this.exp += 25;
      return this.exp;
    },
    checkXp() {
      if (this.lv < 100) {
        this.increment();
        this.lvUp();
      } else {
        this.makeToast("danger", "b-toaster-top-right");
      }
    },
    makeToast(variant = null, toaster) {
      this.$bvToast.toast("Max Level Reached", {
        title: `Error`,
        toaster: toaster,
        variant: variant,
        noCloseButton: true,
        autoHideDelay: 1000,
        solid: true,
      });
    },
    improveStats() {
      this.hp += Math.floor(Math.random() * (4 - 1) + 1);
      this.atk += Math.floor(Math.random() * (3 - 1) + 1);
      this.def += Math.floor(Math.random() * (2 - 1) + 1);
      this.spatk += Math.floor(Math.random() * (3 - 1) + 1);
      this.spdef += Math.floor(Math.random() * (3 - 1) + 1);
      this.speed += Math.floor(Math.random() * (5 - 1) + 1);
    },
  },
};
</script>

<style scoped lang="scss">
$base-color: white;
button {
  &:hover {
    background-color: darken($base-color, 25%);
  }
  border-radius: 4px;
}
</style>
